/*Estilos Generales Body*/
body{
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: whitesmoke;
    font-size: 20px;
}
 /*Estilos del formBox*/
 .box_login{
    padding: 20;
    border-radius: 5px;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 1);
    max-width: 400px;
    width: 100%;
 }
 .box_login label {
    justify-content: center;
    align-items: center;
    color: #00796b;/*color letras del label*/
    font-weight: bold;
    display: block;
    margin-bottom: 5px;
 }
 .box_login input{
    width: 90%;
    padding: 10px;
    margin: auto;
    margin-bottom: 15px;
    border: 1px solid #00796b;
    border-radius: 10px;
 }
 /*Estilos Contenedor de Botones*/

 .containerButtons{
    text-align: center;
    margin-top: 20px;

 }
 .containerButtons button{
    background-color: #00796b;
    color: #ffff;
    padding: 10px 20px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    text-decoration: none;
    margin: 0 10px;
 }
 .containerButtons a:hover, .containerButtons button:hover{
    background-color: #004d40;

 }

 /*Responsividad*/
 @media ( max-width:600px) {
    .box_login{
        padding: 15px;
    }
    .containerButtons a, .containerButtons button{
        width: 100%;
        margin: 10px 0;
    }
 }